export async function loadCities() {
  const response = await fetch(
    "https://api-factory.simbirsoft1.com/api/db/city",
    {
      method: "GET",
      mode: "no-cors",
      headers: {
        "X-Api-Factory-Application-Id": "5e25c641099b810b946c5d5b",
      },
    }
  );

  if (response.ok) {
    return response.json();
  }
}

